<template>
	<div class="page1_5">
		<div class="nav">
			<ul class="fl">
				<li>
					<router-link active-class="active" class="item" :to="{path:'/inside/page1_5'}">
						企业画像
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="交互调整" id="more4_4"/>
			</div> -->
		</div>
		<div class="content_page">
			<div class="page-top" style="clear: both;height: 50px;">
				<Place class="fl" :data="msg"/>
				<div class="fr inter">
					<interBtn title1="审计案例及经验" title="审计经验" id="more1_4" ydname="审计准备"/>
				</div>
			</div>
			<div class="cont box-border-shadow">
				<div v-if="isData" style="position: relative;">
					<div id="graphCharts" style="width: 100%;height: 800px;"></div>
					<div class="msg" style="position: absolute;text-align: left;left: 0;top: 40px;width: 340px;line-height: 34px;">
						<el-row :gutter="10" v-for="item in links">
						  <el-col :md="6" style="color: #b80000;text-align: right;">{{item.name}}:</el-col>
						  <el-col :md="18">{{item.target}}</el-col>
						</el-row>
					</div>
				</div>
				<div v-else>暂无数据</div>
			</div>

		</div>
	</div>
</template>
<script type="text/javascript">
	import interBtn from './interactions'
	import Place from './page/place'
	import * as echarts from 'echarts'
	import reqData from "./../../reqData.js"
	export default{
		name:'page1_4',
		components:{
			interBtn,
			Place
		},
		data(){
			return{
				isData:true,				
				msg:["审计准备","企业画像"],
				links:[]
			}
		},
		computed:{
			company(){
				return JSON.parse(sessionStorage.getItem("company"));
			},
		},
		methods: {
		  open(mes) {
	        this.$notify({
	          title: '确定企业类型',
	          message: mes,
	          type: 'success',
	          duration: 0
	        });
	      },
	      unique(arr,links){
	   		if(arr[0].name===this.company.value){
				arr[0].tag='主体';
			}
			var newArr = [arr[0].name];
	   		var data=[arr[0]];
	   		//去重复项
	  　　 	for(var i=1;i<arr.length;i++){
	　　　　		if(newArr.indexOf(arr[i].name) == -1){
					if(arr[i].name===this.company.value){
						arr[i].tag='主体';
					}
	          　　  newArr.push(arr[i].name);
	          	    data.push(arr[i]);
	    　　  	}
    		}
    		//区分值和主题
    		for(var i=0;i<data.length;i++){
    		 	if(data[i].tag&&data[i].tag==='值'){
    		 		data[i].itemStyle={
			             normal: {
			                 color: '#68bdf6',
			                 borderColor:'#5ca8db',
			                 borderWidth:2,
			             }
			         }
			         data[i].symbol='diamond'
    		 	}
    		}
    		console.log(data);
	        return data;
		  },
		  getData(){
		 	 var graphCharts = echarts.init(document.getElementById('graphCharts'));
		 	 graphCharts.showLoading();

		 	 reqData
		        .req({
		          apiName: "Neo4jToJson",
		           data: {             
		             word:this.company.value,
		             word1:'单位名称'
		           }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            let nodes=data.nodes
					let links=data.links;
					this.links=links;
					if(nodes.length<=0||links.length<=0){
						reqData
					        .req({
					          apiName: "qiyehuaxiang",
					          data: {             
					             ID:this.company.value,
					          }
					        })
					        .then(res => {
					          res = res.data;
					          if (res && res.code === "200") {
					            let data = res.data;
					            console.log(data);
					            reqData
							        .req({
							          apiName: "Neo4jToJson",
							           data: {             
							             word:this.company.value,
							             word1:'单位名称'
							           }
							        })
							        .then(res => {
							          res = res.data;
							          if (res && res.code === "200") {
							            let data = res.data;
							            console.log(data);
							            let nodes=data.nodes
										let links=data.links;
										this.links=links;
										console.log(nodes);
										nodes=this.unique(nodes);
										links.forEach((v)=>{
								    		if($.trim(v.name)=='公司类型'){
								    			console.log(v.target);
								    			this.open(v.target);
								    			sessionStorage.setItem("qiye_type",v.target); 
								    		}else if($.trim(v.name)=='学校类型'){
								    			this.open(v.target);
								    			sessionStorage.setItem("qiye_type",v.target); 
								    		}
								    	})
										// links=this.linksFormat(links);
										graphCharts.hideLoading();
								    	graphCharts.setOption(this.graphOption(nodes,links)); 
							    	    $(window).resize(function(){
											graphCharts.resize({
												width:'auto',
												height:'auto'
											})
									    })
							          }
							        })
							        .catch(err => {
							          console.log(err);
							       });
					          }
					        })
					        .catch(err => {
					          console.log(err);
					          this.isData=false;
					       });
				    }else{
				    	links.forEach((v)=>{
				    		if($.trim(v.name)=='公司类型'){
				    			this.open(v.target);
				    			sessionStorage.setItem("qiye_type",v.target); 
				    		}else if($.trim(v.name)=='学校类型'){
				    			this.open(v.target);
				    			sessionStorage.setItem("qiye_type",v.target); 
				    		}
				    	})
				    	console.log(nodes);
						nodes=this.unique(nodes);
						// links=this.linksFormat(links);
						graphCharts.hideLoading();
				    	graphCharts.setOption(this.graphOption(nodes,links)); 
			    	    $(window).resize(function(){
							graphCharts.resize({
								width:'auto',
								height:'auto'
							})
					    })
				    }

		          }
		        })
		        .catch(err => {
		          console.log(err);
		       });
		  },
		  graphOption(nodes,links){
		    	let option = {
				     title: {
				         text: this.company.value+'企业画像'
				     },
				     tooltip: {
				         formatter: function(x) {
				             return x.data.name;
				         }
				     },
				     series: [{
				         type: 'graph',
				         layout: 'force',
				         focusNodeAdjacency: true,
				         symbolSize: 68,
				         roam: true,
				         edgeSymbol: ['', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 3620,
            				 layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [86, 180],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         itemStyle: {
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             },

				         },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000'

				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 14,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.name;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
				                     return name;
				                 }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>3){
									name=name.substr(0,3)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,3);
									// v.nameSub=name;
								}
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#fff',
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		  }
	    },
	    mounted(){
	    	this.getData();
	    }
	}
</script>
<style type="text/css" scoped="">
	.cont{
		text-align: center; 
		/*margin: 20px auto;*/
		min-height: 600px;
	    padding: 10px 40px;
	    border-radius: 8px;
	    background-color: #fff;
	}
	.cont .leianList>.item{
	    padding: 30px 0;
	    border-bottom: 1px solid #e0e0e0;
	}
	.cont .leianList>.item .btn{
		cursor: pointer;
	}
	.cont .leianList>.item .btn:hover{
		color: #b80000;
	}
	.cont .item p{
	    height: 28px;
	    line-height: 28px;
	    color: #999999;
	}
	.cont .leianList>.item .item-head{
	    margin-bottom: 14px;
	    cursor: pointer;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.cont .leianList>.item .item-head .icon-fl{
	    padding: 7px 12px;
	    background-color: #f59536;
	    border-radius: 5px;
	    color: #fff;
	}
	.cont .leianList>.item .item-head .title{
	    color: #333;
	    font-size: 18px;
	    /*font-weight: bold;*/
	}
	.cont .leianList>.item .item-head .title:hover{
		color: #b80000;
	}
	.cont .leianList>.item .item-b .key{
	    color: #128bed;
	}
	.cont .block{
		text-align: center;
		margin-top: 30px;
		margin-bottom: 80px;
	}
	/*弹出框法律详情样式结束*/
    .flxq .title{
	    color: #000;
	    font-size: 28px;
	    line-height: 60px;
	    text-align: center;
	}
	.flxq .decs{
	    color: #999999;
	    font-size: 14px;
	    line-height: 26px;
	    margin-bottom: 26px;
	    text-align: center;
	}
	.flxq .decs .key,
	.xwxq .decs .key{
	    font-size: 15px;
	    color: #00a2c9;
	}
	.flxq .cont{
	    color: #333;
	    font-size: 16px;
	    text-align:justify;
	    text-indent: 40px;
	    line-height: 34px;
	}
	.flxq .cont p{
	    margin-bottom: 16px;
	}
	/*弹出框法律详情样式结束*/
	.word_main{
		margin: 20 auto;
		overflow: hidden;
	}
	.word_main li{
		float: left;
		line-height: 40px;
		color: #a2a2a2;
	}
	.word_main li.item{
		margin: 0 8px;
		cursor: pointer;
	}
	.word_main li.item:hover{
		color: #b80000;
	}
	.keys{
		overflow: hidden;
	}
	.keys>li{
		float: left;
		display: block;
		background-color: #ccc;
		/*border: 1px solid #30333a;*/
		font-size: 15px;
		color: #30333a;
		padding: 4px 8px;
		border-radius: 5px;
		margin: 10px 5px;
		cursor: pointer;
	}
	.key2>li{
		background-color: #30333a;
		/*border: 1px solid #9195a0;*/
		color: #ccc;
	}
</style>


